(function() {
  'use strict';

  angular
    .module('wayfinder')
    .factory('MapFactory', MapFactory);

  /** @ngInject **/
  function MapFactory($rootScope) {
    var map;
    var mapMarkers = [];
    var bounds = new google.maps.LatLngBounds();
    var directionsService = new google.maps.DirectionsService();
    var directionsDisplay;
    var mapStyleArr = [{
      "featureType": "administrative",
      "elementType": "all",
      "stylers": [{
        "visibility": "on"
      }, {
        "lightness": 33
      }]
    }, {
      "featureType": "landscape",
      "elementType": "all",
      "stylers": [{
        "color": "#f2e5d4"
      }]
    }, {
      "featureType": "poi.park",
      "elementType": "geometry",
      "stylers": [{
        "color": "#c5dac6"
      }]
    }, {
      "featureType": "poi.park",
      "elementType": "labels",
      "stylers": [{
        "visibility": "on"
      }, {
        "lightness": 20
      }]
    }, {
      "featureType": "road",
      "elementType": "all",
      "stylers": [{
        "lightness": 20
      }]
    }, {
      "featureType": "road.highway",
      "elementType": "geometry",
      "stylers": [{
        "color": "#c5c6c6"
      }]
    }, {
      "featureType": "road.arterial",
      "elementType": "geometry",
      "stylers": [{
        "color": "#e4d7c6"
      }]
    }, {
      "featureType": "road.local",
      "elementType": "geometry",
      "stylers": [{
        "color": "#fbfaf7"
      }]
    }, {
      "featureType": "water",
      "elementType": "all",
      "stylers": [{
        "visibility": "on"
      }, {
        "color": "#acbcc9"
      }]
    }];

    var initMap = function() {
      directionsDisplay = new google.maps.DirectionsRenderer();
      map = new google.maps.Map(document.getElementById('map'), {
        zoom: 4,
        center: {
          lat: 40.207518,
          lng: -97.198190
        },
        mapTypeId: google.maps.MapTypeId.ROADMAP,
        disableDefaultUI: true,
        styles: mapStyleArr
      });
      directionsDisplay.setMap(map);
    };

    var fitMapToBounds = function(position) {
      bounds.extend(position);
      map.fitBounds(bounds);
    };

    var calculateRoute = function(start, end) {
      var requestObj = {
        origin: start.getPosition(),
        destination: end.getPosition(),
        travelMode: google.maps.TravelMode.DRIVING
      };
      directionsService.route(requestObj, function(result, status) {
        if (status == google.maps.DirectionsStatus.OK) {
          console.log("here is the direction result", result);
          directionsDisplay.setDirections(result);
        }
      });
    };

    var fac = {
      initiateMap: function() {
        return initMap();
      },
      getMap: function() {
        return map;
      },
      removeMapMarker: function(idx) {
        mapMarkers.splice(idx, 1);
        $rootScope.$apply();
      },
      clearMapMarkers: function() {
        mapMarkers.forEach(function(marker) {
          marker.setMap(null);
        });
        mapMarkers = [];
      },
      getMapMarkers: function() {
        return mapMarkers;
      },
      setMapMarker: function(title, latLngObj, address) {
        var marker = new google.maps.Marker({
          position: latLngObj,
          map: map,
          title: title,
          address: address
        });

        var infowindow = new google.maps.InfoWindow({
          content: '<div class="info-window">' + '<h3 class="info-title">' +
            title + '</h3>' + '<p class="info-description">' + address + '</p>' + '</div>'
        });

        marker.addListener('click', function() {
          infowindow.open(map, marker);
        });

        marker.setMap(map);
        mapMarkers.push(marker);

        if (mapMarkers.length === 2) calculateRoute(mapMarkers[0], mapMarkers[1]);

        fitMapToBounds(latLngObj);
        $rootScope.$apply();
      }
    };

    return fac;
  }
})();

